<script>
    import imgPage from './img'

    export default {
        name: "detail",
        components: {
            imgPage
        },
        data() {
            return {
                show: false,
                minDate: new Date(),
                maxDate: new Date(2025, 10, 1),
                currentDate: new Date(),
                open: false,
                downView: false,
                flexView: false,
                wxView: false,
                model: false,
                imgView: false,
                channel: null,
                courseId: null,
                item: {
                    fileUrl: [],
                    appConfig: {
                        publisherMemberIcon: null
                    }
                },
                timeOut: 30 * 60 * 20 * 60,
                q: {
                    "memberName": "",
                    "memberSymptom": "",
                    "memberAccount": "",
                },
                qr: null,
                iswx: false,
                env: false,
                info: {},
            }
        },
        mounted() {

        },
        created() {
            // 当前上药店宝
            this.env = sessionStorage.getItem('token');
            this.courseId = this.$route.query.courseId;
            this.channel = this.$route.query.channel;
            // 获取详情
            this.detail();
            //获取二维码
            this.storeqrCodeBind();
            //判断wx浏览器还是其他浏览器
            var ua = window.navigator.userAgent.toLowerCase();
            if (ua.match(/MicroMessenger/i) == 'micromessenger') {
                this.iswx = true;
            }
            //任务是否完善
            this.$api.protocolDetail({}).then((res) => {
                this.info = res
            })
        },
        methods: {
            dateConfirm(e) {
                this.q.startTime = this.dateFormat("YYYY-mm-dd HH:MM", e);
                this.$api.update({
                    courseId: this.courseId,
                    startTime: this.q.startTime,
                }).then(res => {
                    this.show = false;
                    this.detail();
                    //获取二维码
                    this.storeqrCodeBind();
                    //判断wx浏览器还是其他浏览器
                    var ua = window.navigator.userAgent.toLowerCase();
                    if (ua.match(/MicroMessenger/i) == 'micromessenger') {
                        this.iswx = true;
                    }
                    //任务是否完善
                    this.$api.protocolDetail({}).then((res) => {
                        this.info = res
                    })
                    this.$toast('修改成功')
                })
            },
            dateFormat(fmt, date) {
                let ret;
                const opt = {
                    "Y+": date.getFullYear().toString(),        // 年
                    "m+": (date.getMonth() + 1).toString(),     // 月
                    "d+": date.getDate().toString(),            // 日
                    "H+": date.getHours().toString(),           // 时
                    "M+": date.getMinutes().toString(),         // 分
                    "S+": date.getSeconds().toString()          // 秒
                    // 有其他格式化字符需求可以继续添加，必须转化成字符串
                };
                for (let k in opt) {
                    ret = new RegExp("(" + k + ")").exec(fmt);
                    if (ret) {
                        fmt = fmt.replace(ret[1], (ret[1].length == 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, "0")))
                    }
                }
                return fmt;
            },
            formatter(type, val) {
                if (type === 'year') {
                    return val + '年';
                }
                if (type === 'month') {
                    return val + '月';
                }
                if (type === 'day') {
                    return val + '日';
                }
                if (type === 'hour') {
                    return val + '时';
                }
                if (type === 'minute') {
                    return val + '分';
                }
                return val;
            },
            // 回放
            huifang() {
                var u = navigator.userAgent;
                var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
                if(isiOS){
                    this.$bs.UDWatchVideo({
                        title: this.item.courseName,
                        url: this.item.liveRecord,
                        isFullScreen: 1,
                    }).then((res) => {
                    })
                }else{
                    window.location.href = 'uniondrugshop://app/base/video?url=' + encodeURIComponent(this.item.liveRecord)
                }

                // sessionStorage.setItem('liveRecord', this.item.liveRecord)
                // sessionStorage.setItem('coursePictureUrl', this.item.coursePictureUrl)
                // this.$router.push('radio')
            },
            // 任务地址
            qianList() {
                this.$router.push('qianList')
            },
            // 样式设置
            topFlex() {
                let u = navigator.userAgent;
                let isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
                if (isiOS) {
                } else {
                    this.flexView = true;
                }
            },
            // 样式设置
            bottomFlex() {
                let u = navigator.userAgent;
                let isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
                if (isiOS) {
                } else {
                    this.flexView = false;
                }
            },
            // 二维码
            storeqrCodeBind() {
                let url = null
                if (window.location.host.includes('localhost')) {
                    url = '.turboradio.cn'
                } else if (window.location.host.includes('turboradio.cn')) {
                    url = '.turboradio.cn'
                } else if (window.location.host.includes('uniondrug.net')) {
                    url = '.uniondrug.net'
                } else if (window.location.host.includes('uniondrug.cn')) {
                    url = '.uniondrug.cn'
                }
                this.$api.generate({
                    "url": 'http://h5-miniapp-education' + url + '/#/yaodianbao/openClass/detail?courseId=' + this.courseId,
                }).then((res) => {
                    this.qr = 'data:image/png;base64,' + res.base64;
                })
            },
            //下载图片
            download() {
                this.downloadFile('药联课程.png', this.qr);
            },
            //下载
            downloadFile(fileName, content) {
                let aLink = document.createElement('a');
                let blob = this.base64ToBlob(content); //new Blob([content]);

                let evt = document.createEvent("HTMLEvents");
                evt.initEvent("click", true, true);//initEvent 不加后两个参数在FF下会报错  事件类型，是否冒泡，是否阻止浏览器的默认行为
                aLink.download = fileName;
                aLink.href = URL.createObjectURL(blob);

                // aLink.dispatchEvent(evt);
                //aLink.click()
                aLink.dispatchEvent(new MouseEvent('click', {bubbles: true, cancelable: true, view: window}));//兼容火狐
            },
            //base64转blob
            base64ToBlob(code) {
                let parts = code.split(';base64,');
                let contentType = parts[0].split(':')[1];
                let raw = window.atob(parts[1]);
                let rawLength = raw.length;

                let uInt8Array = new Uint8Array(rawLength);

                for (let i = 0; i < rawLength; ++i) {
                    uInt8Array[i] = raw.charCodeAt(i);
                }
                return new Blob([uInt8Array], {type: contentType});
            },
            inputChange(num, id) {
                this.q[id] = this.q[id].slice(0, num)
            },
            changeTel(val, memberAccount) {
                val = val.replace(/(^\s*)|(\s*$)/g, "")
                if (!val) {
                    this.q[memberAccount] = "";
                    return
                }
                var reg = /[^\d]/g
                val = val.replace(reg, "")
                this.q[memberAccount] = val;
            },
            UDDownLoadPDF(url, name) {
                let env = sessionStorage.getItem('token');
                if (env) {
                    this.$bs.UDDownLoadPDF({
                        url: url,
                        fileName: name
                    }).then(() => {

                    })
                } else {
                    var ua = window.navigator.userAgent.toLowerCase();
                    if (ua.match(/MicroMessenger/i) == 'micromessenger') {
                        this.downView = true;
                    } else {
                        let mywin = window.open('', '_self');
                        mywin.location.href = url;
                    }
                }
            },

            modelFn(e, joinStatus) {
                let env = sessionStorage.getItem('token');
                if (env) {
                } else {
                    var ua = window.navigator.userAgent.toLowerCase();
                    if (ua.match(/MicroMessenger/i) == 'micromessenger') {
                    } else {
                        this.$toast('请在微信打开！')
                        return false;
                    }
                }
                if(this.item.courseType == '2'){
                    if (this.item.subscribeInfo == '1') {
                        this.join(e, joinStatus)
                    } else {
                        this.model = true;
                    }
                }else {
                    this.join(e, joinStatus)
                }
            },

            join(e, joinStatus) {
                // 获取环境是否在app里
                let env = sessionStorage.getItem('token');
                if (env) {
                    // 在app中需要判断是否完善信息
                    if (this.item.isFullInfo == '0') {
                        this.$toast('请完善用户信息！')
                        this.$router.push('/yaodianbao/openClass/inofUp')
                        return false
                    }
                }

                // 预约弹框需要验证
                if (joinStatus == '1' && this.item.subscribeInfo == '0' && this.item.courseType == '2') {
                    if (!this.q.memberName) {
                        this.$toast('请输入姓名')
                        console.log(6)
                        return false
                    }
                    if (!this.q.memberAccount) {
                        this.$toast('请输入手机号')
                        console.log(7)
                        return false
                    }
                    if (!this.q.memberSymptom) {
                        this.$toast('请输入症状')
                        console.log(7)
                        return false
                    }
                }

                // 预约接口
                this.$api.join({
                    "joinStatus": joinStatus,
                    "courseId": this.courseId,
                    "takeType": e,
                    ...this.q
                }).then(res => {
                    // 获取详情
                    this.detail();
                    // 关闭弹框
                    this.model = false;
                    // 修改预约状态
                    this.item.subscribeInfo = '1';
                    // 如果直播已经开始就执行
                    if (this.item.liveStatus == '1') {
                        this.live();
                    }
                })
            },
            courseTypeFn(type){
                if(type === '1'){
                    return '学术会'
                }
                if(type === '2'){
                    return '患教会'
                }

                if(type === '3'){
                    return '连锁内训'
                }
            },
            collection(e) {
                let env = sessionStorage.getItem('token');
                if (env) {
                    if (this.item.isFullInfo == '0') {
                        this.$toast('请完善用户信息！')
                        this.$router.push('/yaodianbao/openClass/inofUp')
                        return false
                    }
                }
                this.$api.collection({
                    "courseId": this.courseId,
                    "collectionStatus": e
                }).then(res => {
                    this.detail();
                })
            },
            httpFn(e) {
                console.log(e)
                e.replace(/http:/, "https:")
                return e

            },
            imgFn() {
                this.imgView = false;
            },
            live() {
                // 判断当前环境是否在app内
                let env = sessionStorage.getItem('token');
                if (env) {
                    // app内需要完善信息
                    if (this.item.isFullInfo == '0') {
                        this.$toast('请完善用户信息！')
                        this.$router.push('/yaodianbao/openClass/inofUp')
                        return false
                    }
                } else {
                    // 如果在微信浏览器内 继续执行，非微信浏览器并且非app内 弹框
                    var ua = window.navigator.userAgent.toLowerCase();
                    if (ua.match(/MicroMessenger/i) == 'micromessenger') {
                    } else {
                        this.wxView = true;
                        return false
                    }
                }
                // 只有患教会展示
                if (this.item.courseType == '2') {
                    // 没有预约 展示预约弹框
                    if (this.item.myPublish == '0') {
                        console.log('12321321321:', this.item.subscribeInfo)
                        if (this.item.subscribeInfo == '0') {
                            this.model = true;
                            return false
                        }
                    }
                }

                let url;
                if (window.location.host.includes('localhost')) {
                    url = '.turboradio.cn'
                } else if (window.location.host.includes('turboradio.cn')) {
                    url = '.turboradio.cn'
                } else if (window.location.host.includes('uniondrug.net')) {
                    url = '.uniondrug.net'
                } else if (window.location.host.includes('uniondrug.cn')) {
                    url = '.uniondrug.cn'
                }

                // 主播并且开始直播
                if ((this.item.liveStatus == '1' && this.item.myPublish == '1')) {
                    this.q = {
                        "memberName": "",
                        "memberSymptom": "",
                        "memberAccount": "",
                    };
                    this.$bs.UDOpenLive(this.item.appConfig).then((res) => {
                    })
                }
                // 用户观看直播
                if (this.item.liveStatus == '1' && this.item.myPublish == '0') {
                    let startTime = new Date(this.item.startTime.replace(/-/g, "/"))
                    let date = new Date()
                    console.log(startTime.getTime())
                    console.log(date.getTime())
                    // 课程还没有开始禁止进入直播
                    if (startTime.getTime() - date.getTime() > 0) {
                        this.$dialog.alert({
                            message: '课程将于' + this.item.startTime + '开始，请等待讲师开始课程。',
                        }).then(() => {
                            // on close
                        });
                        return false
                    }
                    this.$api.join({
                        "joinStatus": '1',
                        "courseId": this.courseId,
                        "takeType": '1',
                    }).then(res => {
                        //记录观看人数
                        this.$api.watch({
                            "courseId": this.courseId,
                        }).then(res => {
                            this.q = {
                                "memberName": "",
                                "memberSymptom": "",
                                "memberAccount": "",
                            };
                            let env = sessionStorage.getItem('token');
                            // APP中进入app方法
                            if (env) {
                                if(this.item.liveServer == '2'){
                                    this.$bs.UDWatchLiveAgora(this.item.appConfig).then((res) => {
                                    })
                                }else {
                                    this.$bs.UDWatchLive(this.item.appConfig).then((res) => {
                                    })
                                }
                            } else {
                                // 非app进去h5
                                window.location.href = 'http://h5-middle-live' + url + '/#/roomChat?roomId=' +
                                    this.item.appConfig.chatRoomId +
                                    '&uid=' + this.item.appConfig.imAccId +
                                    '&sdkToken=' + this.item.appConfig.imToken +
                                    '&courseId=' + this.courseId +
                                    '&chatroomNick=' + this.item.appConfig.nickName
                            }
                        })
                    })

                }
            },

            pushNav() {
                this.$router.push('/yaodianbao/openClass/push?courseId=' + this.item.courseId)
            },
            detail() {
                this.$api.detail({
                    "courseId": this.courseId
                }).then(res => {
                    this.item = res;
                    if (res.fileUrl) {
                        this.item.fileUrl = JSON.parse(this.item.fileUrl);
                    }
                    console.log(this.item.liveStatus == '0')
                    if (this.item.liveStatus == '0') {

                        if (this.item.isAfterFiveMin == '1') {
                            this.item.liveStatus = '1'

                        }
                    }
                })
            },
            liveStatusFn(e) {
                if (this.item.dataStatus == '0' || this.item.dataStatus == '2') {
                    return this.item.startTime
                }
                if (e == '1') {
                    return '正在上课'
                }
                if (e == '0') {
                    return '课程未开始'
                }
                if (e == '2') {
                    return '课程已结束'
                }

            }
        }
    }
</script>

