<template>
    <div class="view">
        <div>
            <div class="page" id="view" ref="imageWrapper">
                <img class="bg" src="./img/share_pic_bg@2x.png" alt="">
                <div style="position: relative;z-index: 99">
                    <div class="title">
                        <div class="tag">{{ item.courseTypeText }}</div>
                        <div style="background: #00B095;width: 100%;height: 100%">
                            <img crossOrigin="anonymous" :src="httpFn(item.coursePictureUrl + '?x-oss-process=image/resize,w_600/quality,q_80')" alt="">
                        </div>
                    </div>
                    <div class="kaike">
                        <h3>{{ item.courseName }}</h3>
                        <div>
                            <img src="../img/live_icon_time@2x.png" alt="">
                            <p>{{ item.startTime }} 开课</p>
                        </div>
                    </div>
                    <div class="qr">
                        <div class="qr-left">
                            <h3>{{ item.teacherName }}</h3>
                            <p>{{ item.teacherTitle }}</p>
                            <h4>邀您来听直播课</h4>
                        </div>
                        <img :src="qr" alt="">
                    </div>
                </div>
            </div>
            <div class="fen">
                <img src="./img/more_wechat@2x.png" alt="" @click.stop="uploadfile(1)">
                <img src="./img/more_moments@2x.png" alt="" @click.stop="uploadfile(2)">
            </div>
        </div>
    </div>
</template>

<script>
    import html2canvas from "html2canvas";
    import QRCode from 'qrcodejs2' // 引入qrcode
    export default {
        name: "imgview",
        props: ['item'],
        data() {
            return {
                imgUrl: '',
                shareImg: '',
                qr: null
            }
        },
        created() {
            console.log(this.item)
            this.$loading.show();
            this.storeqrCodeBind();
        },
        methods: {
            storeqrCodeBind() {
                let url;
                let envx = sessionStorage.getItem('_env');
                if (envx=='1') {
                    url = '.turboradio.cn'
                }
                if (envx=='2') {
                    url = '.uniondrug.net'
                }
                if (envx=='3') {
                    url = '.uniondrug.cn'
                }
                this.$api.generate({
                    "url": 'http://h5-miniapp-education'+ url +'/#/yaodianbao/openClass/detail?courseId=' + this.item.courseId,
                }).then((res) => {
                    console.log(res);
                    this.qr = 'data:image/png;base64,' + res.base64;
                    this.$loading.show();
                    this.getMarketingTemplateDefault();
                })
            },
            httpFn(e) {
                console.log(e)
                e.replace(/http:/, "https:")
                return e
            },

            getMarketingTemplateDefault() {
                this.$nextTick(()=>{
                    setTimeout(() => {
                        this.toImage();
                    }, 1500)
                })
            },

            //上传照片
            uploadfile(type) {
                this.$api.uploadfile(this.base64ToFile(this.imgUrl, new Date() + '.png')).then((res) => {
                    console.log(res);
                    this.shareImg = res.url;
                    this.setShare(type)
                })
            },

            //分享
            setShare(type) {
                let shareDate = {
                    shareLink: location.href,
                    shareTitle: document.title,
                    shareDescr: document.title,
                    shareImg: this.shareImg,
                    shareType: type,
                    shareBigImg: 1,
                }

                this.$bs.wxShare(shareDate).then((e) => {
                    if (e.status == '1') {
                        this.$toast('分享成功')
                    } else if (e.msg) {
                        this.$toast(e.msg)
                    }
                })
            },

            //转成文件
            base64ToFile(urlData, fileName) {
                this.$loading.show();
                let arr = urlData.split(',');
                let mime = arr[0].match(/:(.*?);/)[1];
                let bytes = atob(arr[1]); // 解码base64
                let n = bytes.length
                let ia = new Uint8Array(n);
                while (n--) {
                    ia[n] = bytes.charCodeAt(n);
                }
                return new File([ia], fileName, {type: mime});
            },

            toImage() {
                console.log(1)
                let o = document.getElementById("view");
                //获取此元素的高度
                let height = o.offsetHeight; //高度
                //获取此元素的宽度
                let width = o.offsetWidth; //宽度
                console.log(height);
                console.log(width);
                html2canvas(this.$refs.imageWrapper, {
                    allowTaint: false,
                    width: width,
                    height: height,
                    backgroundColor: null,
                    useCORS: true, // 允许图片跨域
                    timeout: 1000 // 加载延时
                }).then(canvas => {
                    let dataURL = canvas.toDataURL("image/png");
                    this.imgUrl = dataURL;
                    this.$loading.hide();
                }).catch((e) => {
                    console.log(e);
                });
            },
            courseTypeFn(type){
                if(type === '1'){
                    return '学术会'
                }
                if(type === '2'){
                    return '患教会'
                }

                if(type === '3'){
                    return '连锁内训'
                }
            },
        }
    }

</script>

